import React, { useEffect, useState, useContext, useRef } from "react";
import { userContext } from "../App";
import {
  MapContainer,
  TileLayer,
  ZoomControl,
  Marker,
  Popup,
} from "react-leaflet";
import CustomMarker from "../components/CustomMarker";

//import fullscreen mode main js
import "leaflet-fullscreen/dist/Leaflet.fullscreen.js";
//import fullscreen mode css
import "leaflet-fullscreen/dist/leaflet.fullscreen.css";
// import compability css file
import "leaflet-defaulticon-compatibility/dist/leaflet-defaulticon-compatibility.css";
import "leaflet-defaulticon-compatibility";
//import leaflet css
import "leaflet/dist/leaflet.css";

const MAP_POSITION = [0, 0];
const MARKER_POSITION = [52.49913882549316, 13.418318969833383];
const OFLINE_URL = "./map/{z}/{x}/{y}.png";

function Map() {
;
  const [marker] = useContext(userContext);

  let [data, setData] = useState([]);

  const saveMarkers = (newMarkerCoords) => {
    const newLocation = [data, newMarkerCoords];
    setData(() => ({ newLocation }));
  };

  useEffect(() => {


   

    //positioning of controller
    document
      .querySelector(".leaflet-control-container")
      .firstChild.classList.replace("leaflet-left", "leaflet-right");

    document.querySelector(".leaflet-control-container").firstChild.style.top =
      "14vh";
    // set custom attribute
    document
      .querySelector(".leaflet-control-attribution")
      .children[0].setAttribute("href", "https://fazlulkarim.netlify.app/");
    document
      .querySelector(".leaflet-control-attribution")
      .children[0].setAttribute("target", "_blank");
    document.querySelector(
      ".leaflet-control-attribution"
    ).children[0].innerText = "Develop By Md. Fazlul Karim";
  }, []);

  return (
    <div>
      <MapContainer
        // whenCreated={(map) => {
        //   // mapRef.current = map;

        // }}

        center={MAP_POSITION}
        minZoom={2}
        zoom={2}
        maxZoom={5}
        zoomControl={false}
        scrollWheelZoom={true}
        fullscreenControl={true}
        style={{
          width: "100vw",
          height: "100vh",
          margin: "0 auto",
        }}
      >
        <TileLayer url={OFLINE_URL} noWrap="true" continuosWorld="false" />
        <Marker position={MARKER_POSITION}>
          <Popup>Hello I'm a popup!</Popup>
        </Marker>

        {marker ? <CustomMarker saveMarkers={saveMarkers} /> : ""}
        <ZoomControl position="topright" />
      </MapContainer>
    </div>
  );
}

export default Map;
